import React, { memo } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Grid } from '@material-ui/core';
import ErrorIcon from '@material-ui/icons/Error';
import PropTypes from 'prop-types';

import Header from '../../components/Header';
import withViewerData from '../../utils/with-viewer-data';
import styles from './styles';

const NotAuthorizedMessageBase = memo(({
  messageText, classes
}) => (
  <div className={classes.root}>
    <Header />
    <section>
      <div className={classes.container}>
        <Grid container spacing={1}>
          <div className={classes.centerHeaderView}>
            <ErrorIcon className={classes.emptyHeaderIcon} />
            <b className={classes.emptyHeaderTitle}>
              {messageText}
            </b>
          </div>
        </Grid>
      </div>
    </section>
  </div>
));
NotAuthorizedMessageBase.propTypes = {
  classes: PropTypes.object
};

const NotAuthorizedMessage = withStyles(styles, { withTheme: true })(NotAuthorizedMessageBase);

const withNotAuthorizedHandler = ({
  condition,
  messageText = 'Voc no possui permisso de acesso a esta tela.'
} = {}) => WrappedComponent => {
  const HOC = withViewerData((props) => {
    if (!props.viewer) return null;

    if (condition(props.viewer)) {
      return <NotAuthorizedMessage messageText={messageText} />;
    }
    return <WrappedComponent {...props} />;
  });
  return HOC;
};

export default withNotAuthorizedHandler;
